import React, { useState, useEffect } from "react";
import "./Header.css";

export default function Header() {
  const [isNavVisible, setNavVisibility] = useState(false);
  const [isSmallScreen, setIsSmallScreen] = useState(false);

  

  useEffect(() => {
    const mediaQuery = window.matchMedia("(max-width: 700px)");
    mediaQuery.addEventListener('change', handleMediaQueryChange);
    handleMediaQueryChange(mediaQuery);

    return () => {
      mediaQuery.removeEventListener(handleMediaQueryChange);
    };
  }, []);

  const handleMediaQueryChange = mediaQuery => {
    if (mediaQuery.matches) {
      setIsSmallScreen(true);
    } else {
      setIsSmallScreen(false);
    }
  };

  const toggleNav = () => {
    setNavVisibility(!isNavVisible);
  };

  return (
    <header className="Header">
      <img src={require("../kuvat/logo.png")} className="Logo" alt="logo" />
      {(!isSmallScreen || isNavVisible) && (
        <nav className="Nav">
        <a href="/cooking">Cooking</a>
        <a href="/alchemy">Alchemy</a>
        <a href="/gathering">Gathering</a>
        <a href="/farming">Farming</a>
        <a href="/training">Training</a>
        <a href="/sailing">Sailing</a>
        <a href="/laskurit">Laskurit</a>
        <a href="/yhteys">Ota Yhteyttä</a>
        </nav> )}
      <button onClick={toggleNav} className="Burger">☰</button>
    </header>
  );
}